@import "styles/config";

.chat-search {
	position: relative;
	font-size: 14px;
}

.chat-search__control {
	width: 100%;
	background-color: $gray-400;
	border: 1px solid transparent;
	padding: 0.75em;
	padding-left: 2.5em;
	overflow: hidden;
	border-radius: 4px;
	transition: all 300ms;

	&:focus {
		outline: 0;
		border-color: $primary;
	}
}

.chat-search__icon {
	position: absolute;
	top: 50%;
	left: 1em;
	width: 1em;
	transform: translateY(-50%);
}
