.auth-form {
  display: flex;
  flex-direction: column;
	padding: 2rem 1.5rem;
	width: 100%;
	min-height: 450px;
	max-width: 380px;
	border-radius: 1rem;
	box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
}

.auth-form__actions {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: auto;
}

.auth-form__title {
  margin-top: 0;
  margin-bottom: 1rem;
  text-align: center;
}

.auth-form__submit {
  width: 100%;
  margin-bottom: .5rem;
}
